import React, { useState } from "react";
import { Formik, Form, Field, ErrorMessage } from "formik";

const AuthForm = () => {
  const [operation, setOperation] = useState("Login");
  return (
    <Formik
      initialValues={{ email: "", password: "", name: "" }}
      validate={values =>{
          const errors={};
          if (!values.email) {
           errors.email = 'Required';
         } else if (
           !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)
         ) {
           errors.email = 'Invalid email address';
         }
         return errors;
      }}
      onSubmit={(user) => {
          console.log(user)
      }}
    >
      {(f) => console.log(f)}
      {/* ( */}
        {/* <div>
          <h1>Any place in your app!</h1>
          <Form>
            <Field name="email" />
            {error ? error.email:null}
            <Field name="password" />
            {error ? error.password:null}
            <label>
              <input
                type="radio"
                onChange={(e) => setOperation("Register")}
                checked={operation === "Register"}
                name="radio"
                value="sign up"
              />
              Sign up
              
            </label>

            <label>
              <input
                type="radio"
                onChange={(e) => setOperation("Login")}
                checked={operation === "Login"}
                name="radio"
                value="sign in"
              />
              Sign in
            </label>
            <Field name="name" />
            <button type="submit">Submit</button>
          </Form>
        </div>
      )} */}
    </Formik>
  );
};

export default AuthForm;
